.modal-body {
	max-height: 500px;
}
@media(max-width: 767px) {
	.modal-body {
		height: 300px;
	}
}

.zoom-standard(@zoom:117%, @inverseZoom:80%, @dialogWidth:721px){
	-ms-text-size-adjust: auto;
	-webkit-text-size-adjust: auto;
	zoom:@zoom;
	.modal{
		zoom:@inverseZoom;
		.modal-header{
			zoom:@zoom;
		}
		.modal-footer{
			zoom:@zoom;
		}
		.modal-body{
			zoom:@zoom;
		}
		&.accounts-guide-modal{
			.modal-dialog{
				width:@dialogWidth;
			}
		}

		// .box{
		// 	zoom:@zoom;
		// 	.box{
				/*这里是为了兼容出现两层box嵌套的情况*/
		// 		zoom:100%;
		// 	}
		// }
		// &.forward-select-flow-modal{
		// 	.modal-body>div{
		// 		zoom:@zoom;
		// 	}
		// }
		// &#instance_cc_modal{
		// 	.modal-body>form{
		// 		zoom:@zoom;
		// 	}
		// }
		// .admin-dashboard-body>form{
		// 	zoom:@zoom;
		// }
		// .modal-body>.weui_grids{
		// 	zoom:@zoom;
		// }
		// .mail-account-modal{
		// 	.modal-body>form{
		// 		zoom:@zoom;
		// 	}
		// }
	}
}

.zoom-level(normal){
	@media (max-width: 767px){
		.zoom-standard(115%, 85%, 708px);
	}
}
.zoom-level(large){
	.zoom-standard();
	@media (max-width: 767px){
		.zoom-standard(130%, 76%, 800px);
	}
}
.zoom-level(extra_large){
	.zoom-standard(135%, 74%, 811px);
	@media (max-width: 767px){
		.zoom-standard(145%, 65%, 900px);
	}
}

.zoom-normal{
	.zoom-level(normal);
}
.zoom-large{
	// .zoom-level(large);
	.zoom-level(normal);
}
.zoom-extra-large{
	// .zoom-level(extra_large);
	.zoom-level(normal);
}


