/*字体放大时，点击按钮后下方的菜单在小屏幕下显示不全*/
@media (max-width: 767px) {	
	ul.dropdown-menu.dropdown-menu-right {
		width: auto;
		max-width: 200px;
		.menu-item-help {
			min-width: 140px;
		}
	}
}

